<template>
  <v-container class="container overflow-h">
    
    <!-- *********** Logo ************** -->
    <section class="d-flex justify-center pt-5 pb-5 bg-custom elevation-2 header">
      <img
        height="80"
        src="../assets/rio-mare-logo.png"
      >
    </section>

    <!-- *************  Filiera tracciata    *************** -->
    <section class="d-flex flex-column pa-5 mt-30">
      <h1 class="text-center text-custom"><b>SCOPRI LA PROVENIENZA DEL TUO TONNO</b></h1>
      <p class="mt-3 ma-0">Ti sei mai chiesto da dove arriva il tonno che metti sulla tua tavola? Con Rio Mare è tutto chiaro, 
        perché puoi conoscere tutta la sua storia: la sua specie, l'area di pesca, il metodo di pesca utilizzato e il nome della 
        nave che l'ha pescato. Così il prodotto che arriva sulla tua tavola ha sempre un nome, un cognome e un indirizzo!
        <br> <br>
        Con questo servizio online, attraverso semplici e brevi step, potrai richiedere informazioni sull'origine del pesce utilizzato in qualsiasi confezione di tonno Rio Mare prodotta a partire dal 1° gennaio 2018.
        Ricorda che puoi tracciare il tuo prodotto solo se sei munito del codice presente sulla confezione.
        </p>
    </section>

    <!-- ***************  Select Product    **************** -->
    <section class="pa-5 pt-2">
      <label for="">
        Seleziona la categoria di prodotto che stai scansionando:
      </label>
      <v-select
      class="mt-2"
          v-model="productType"
          :items="itemsType"
          :rules="rules"
          label="Seleziona la categoria di prodotto"
          item-text="name"
          item-value="id"
          solo
          clearable
        ></v-select>
        <label for="">
        Seleziona il prodotto che stai scansionando:
      </label>
      <v-select
      v-if="productType"
      class="mt-2"
          v-model="product"
          :items="items"
          :rules="rules"
          label="Seleziona prodotto"
          item-text="name"
          item-value="product_id"
          solo
        ></v-select>
    </section>


    <!--  ******************   Button Footer    ********************* -->
    <section class="pl-5 pr-5 button-footer">
      <v-row>
      <v-col cols="12">
        <!-- <router-link to="/scan_code"> -->
        <v-btn
          block
          color="primary"
          elevation="5"
          large
          @click="next"
          :disabled="!product || !productType"
        >
        Scansiona Codice
        </v-btn>
        <!-- </router-link> -->
      </v-col>
      <v-col cols="12" style="padding: 0">
        <p class="text-center ma-0">
          <b>o</b>
        </p>
      </v-col>
      <v-col cols="12">
        <v-btn
          block
          color="primary"
          elevation="5"
          large
          :disabled="disabled"
          @click="insertManualData"
        >
        Inserisci manualmente
        </v-btn>

      </v-col>
    </v-row>
    </section>
    
  </v-container>
</template>

<script>
import axios from 'axios'

  export default {
    name: 'HomeComponent',
    data () {
      return {
        product: null,
        productType: null,
        products: [],
        productsOriginal: [],
        productTypes: [],
        rules: [
          v => !!v || 'Seleziona un prodotto',
        ]
      }
    },
    computed: {
      items () {
        return this.products
      },
      itemsType () {
        return this.productTypes
      },
      disabled () {
        if (!this.product || !this.productType) {
          return true
        } else {
          return false
        }
      }
    },
    watch: {
      productType (v) {
        const id = v
        this.products = this.productsOriginal.filter(v => {
          return v.productTypeId == id 
        })
      }
    },
    async mounted () {
      sessionStorage.removeItem('product')
      sessionStorage.removeItem('ItemDetails')
      const resType = await axios.get('https://backend-bolton.herokuapp.com/productsTypes')
      this.productTypes = resType.data
      const res = await axios.get('https://backend-bolton.herokuapp.com/products')
      this.productsOriginal = res.data
    },
    methods: {
        next () {
          sessionStorage.setItem('product', this.product)
          this.$router.push('/scan_code')
        },
         insertManualData () {
          sessionStorage.setItem('product', this.product)
          this.$router.push('/insert_code')
        }
      }
  }
</script>
